.uploader {
  z-index: 5;
  -webkit-box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 50%;
  height: 50%;
  top: 25%;
  left: 50%;
  padding: 20px;
  transform: translate(-50%, 0);
  background-color: var(--primary);
  border-radius: 16px;
 
}

.uploader-image{
    overflow: scroll;
    height:90%;
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.uploader-image::-webkit-scrollbar {
    display: none;
  }

  .center{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  @media (max-width: 468px) {
    .uploader {
      z-index: 5;
      -webkit-box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
      -moz-box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
      justify-content: center;
      align-items: center;
      position: fixed;
      width: 80%;
      height: 50%;
      top: 20%;
      left: 50%;
      padding: 20px;
      transform: translate(-50%, 0);
      background-color: var(--primary);
      border-radius: 16px;
     
    }
  }